.time-progress {
  position: absolute;
  height: 2px;
  bottom: 0;
  width: 100%; }
  .time-progress__progress {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transition: 1500ms width;
    -o-transition: 1500ms width;
    transition: 1500ms width;
    height: 100%;
    background: #3359db; }
    .time-progress__progress.success {
      background: green; }
    .time-progress__progress.failure {
      background: red; }



